body {
  margin-top: 20px;
  font-size: .875rem;
}

canvas{
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

nav.col-md-2.d-none.d-md-block.sidebar {
  background-color: #eff1f2;
}

.chart {
  position: relative;
  margin: auto;
  width:100%;
}

.chart.timeSeries {
  height:60vh;
}

.chart.barGraph {
  height: 40vh;
}

.multiTopBar > .col-sm > .chart.barGraph {
  height: 40vh;
}

input.movingAverage {
  padding-top: 10px;
}

input.movingAverage[type=range]::-webkit-slider-thumb {
  background: #919090;
}
input.movingAverage[type=range]::-moz-range-thumb {
  background: #919090;
}
input.movingAverage[type=range]::-ms-thumb {
  background: #919090;
}

div.nav-link.top-tab-link {
  cursor: pointer;
}

div.top-tab-container {
  margin-top: 25px;
  margin-bottom: 25px;
}

div.top-tab-container > div.row > div.col-sm > div.chart.barGraph.topx {
  height: 45vh;
}

th.sortableTableHeader:hover {
  background-color: #fdf2b7 !important;
  cursor: pointer;
}

th.sortableTableHeader.sorted {
  background-color: #d4d7db;
}

td.selectedForSortColumn {
  font-weight: bold;
}

/* Desktop size */
@media (max-width: 575.98px) { 
  .chart.barGraph {
    height:40vh;
  }
  .chart.barGraph.topx {
    height:40vh;
  }
}

/* Mobile size */
@media (max-width: 767.98px) { 
  .chart.barGraph {
    height:40vh;
  }

  .chart.barGraph.topx {
    height:40vh;
  }

  .chart.provinces.barGraph {
    height:58vh;
  }

  .navbar-brand {
    margin-left: 12px !important;
  }

  .nav-item {
    margin: 0px 11px 0px 11px;
  }
}

.provinceNav {
  margin-bottom: 15px;
}

.nav-item.provinceNavLink {
  background-color: #e4eeff;
  border-radius: 5px;
  margin-right: 4px;
  margin-bottom: 4px;
}

.nav-item.provinceNavLink.active {
  background-color: #afccfd;
}

.nav-item.provinceNavLink:hover {
  background-color: #f3f9ff;
}

.heatMapHeader {
  text-align: center;
  margin-bottom: 7px;
  font-weight: 400;
}

.confirmedText {
  color: #721c24;
}

.activeText {
  color: #004085;
}

.recoveredText {
  color: #155724;
}

.deathsText {
  color: #1b1e21;
}

.confirmedTodayText {
  color: #5b037b;
}

.deathsTodayText {
  color: #856404;
}

.lastUpdatedTimeStamp {
  font-size: 11px;
  color: #777;
}

.alert-purple {
  color: #5b037b;
  background-color: #ddc4fb;
  border-color: #cbabf2;
}

.text-purple {
  color: #5b037b;
}

.footer {
  font-size: 11px;
  background-color: #e3e3e3;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}

/*
 * Sidebar
 */

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 48px 0 0; /* Height of navbar */
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .sidebar-sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #999;
}

.sidebar .nav-link.active {
  color: #007bff;
}

.sidebar .nav-link:hover .feather,
.sidebar .nav-link.active .feather {
  color: inherit;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}

/*
 * Content
 */

[role="main"] {
  padding-top: 95px; /* Space for fixed navbar */
}

@media (min-width: 768px) {
  [role="main"] {
    padding-top: 48px; /* Space for fixed navbar */
  }
}

/*
 * Navbar
 */

.navbar-brand {
  padding-top: .75rem;
  padding-bottom: .75rem;
  margin-left: 20px;
  font-size: 1rem;
}

.navbar .form-control {
  padding: .75rem 1rem;
  border-width: 0;
  border-radius: 0;
}

.form-control-dark {
  color: #fff;
  background-color: rgba(255, 255, 255, .1);
  border-color: rgba(255, 255, 255, .1);
}

.form-control-dark:focus {
  border-color: transparent;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, .25);
}

[data-tip] {
  position:relative;

}

[data-tip]:before {
  content:'';
  /* hides the tooltip when not hovered */
  display:none;
  content:'';
  display:none;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid #1a1a1a;
  position:absolute;
  top:30px;
  left:35px;
  z-index:8;
  font-size:0;
  line-height:0;
  width:0;
  height:0;
  position:absolute;
  top:30px;
  left:35px;
  z-index:8;
  font-size:0;
  line-height:0;
  width:0;
  height:0;
}

[data-tip]:after {
  display:none;
  content:attr(data-tip);
  position:absolute;
  top:35px;
  left:0px;
  padding:0px 8px;
  background:#1a1a1a;
  color:#fff;
  z-index:9;
  font-size: 0.75em;
  height:18px;
  line-height:18px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  white-space:nowrap;
  word-wrap:normal;
}

[data-tip]:hover:before,
[data-tip]:hover:after {
  display:block;
}
